import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import {HomeComponent} from './irlowhome/irlowhome.component';

import { ResultComponent } from './irlowresult/irlowresult.component';
import { NavComponent } from './nav/nav.component';
import {AppService} from './app.service';
import { FilterimagesPipe } from './filterimages.pipe';

const appRoutes: Routes =[
  {path: '', component:HomeComponent},
  {path: 'irlowhome', component:HomeComponent},
  {path: 'irlowresult', component:ResultComponent},
  { path: '**', redirectTo: '' },
];

@NgModule({
  declarations: [
    AppComponent,
    FilterimagesPipe,
    HomeComponent,
    ResultComponent,
    NavComponent
  ],

  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes)
  ],
  providers: [AppService, HomeComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }



